export default function SyntheticEvent(
  bulbX: [] = [],
  bulbY: [] = [],
  clientX: number = 0,
  clientY: number = 0,
  selectedData: object,
  xIdx: number = 0,
): void {
  this.bulbX = bulbX;
  this.bulbY = bulbY;
  this.clientX = clientX;
  this.clientY = clientY;
  this.selectedData = selectedData;
  this.xIdx = xIdx;
}
